import React from "react";
import styles from "./Card.module.css";
import logo from "../img/todo.png";

const Card = () => {
  return (
    <section className={styles.card}>
      <p className={styles.card_title}>To Do List</p>
      <img src={logo} alt={"Todo logo"}></img>
      <p className={styles.card_descripton}>
        Simple app for completing tasks.
        <br />
        Made with HTML, CSS and JS.
      </p>
    </section>
  );
};

export default Card;
